body {
  background-color: #717979;
}

.loader {
  width: 100%;
  max-width: 6rem;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
  margin-bottom: 3rem;
  display: flex;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  filter: drop-shadow(0 0 1rem rgba(255, 255, 255, .75));
  border-radius: 50%;
  animation: pulsOut 1.8s ease-in-out infinite;
  position: absolute;
}

.loader:before {
  width: 100%;
  padding-bottom: 100%;
  animation-name: pulsIn;
  box-shadow: inset 0 0 0 1rem #fff;
}

.loader:after {
  width: calc(100% - 2rem);
  padding-bottom: calc(100% - 2rem);
  box-shadow: 0 0 #fff;
}

@keyframes pulsIn {
  0% {
    opacity: 1;
    box-shadow: inset 0 0 0 1rem #fff;
  }

  50%, 100% {
    opacity: 0;
    box-shadow: inset 0 0 #fff;
  }
}

@keyframes pulsOut {
  0%, 50% {
    opacity: 0;
    box-shadow: 0 0 #fff;
  }

  100% {
    opacity: 1;
    box-shadow: 0 0 0 1rem #fff;
  }
}

.inviseble {
  display: none;
}

.loader {
  position: relative;
  top: 60px;
  left: 120px;
}

.cat-info {
  gap: 40px;
  margin-top: 40px;
  display: flex;
}

.cat-container {
  width: 500px;
}

.name {
  margin-top: 0;
  font-size: 50px;
}

.discription, .temperament {
  font-size: 20px;
}

